/**
 * Transition Checklist Page JavaScript
 *
 * This file contains the JavaScript for the Transition Checklist page.
 */

// Import components
import Accordion from '../components/accordion';
import { saveToLocalStorage, getFromLocalStorage } from '../utils/storage';

/**
 * Initialize the page
 */
function initPage() {
  console.log('Transition Checklist page initialized');
  
  // Initialize accordion if there are any on the page
  Accordion.init({
    triggerSelector: '.checklist-accordion-header',
    contentSelector: '.checklist-accordion-content',
    closeOthers: false,
    animateHeight: true
  });
  
  // Set up checklist functionality
  setupChecklist();
  
  // Set up progress tracking
  setupProgressTracking();
}

/**
 * Set up checklist functionality
 */
function setupChecklist() {
  // Get all checkboxes
  const checkboxes = document.querySelectorAll('.checklist-item input[type="checkbox"]');
  
  // Load saved states from localStorage
  checkboxes.forEach(checkbox => {
    const savedState = getFromLocalStorage(`checklist-${checkbox.id}`);
    if (savedState === 'true') {
      checkbox.checked = true;
    }
    
    // Add event listener to save state on change
    checkbox.addEventListener('change', function() {
      saveToLocalStorage(`checklist-${this.id}`, this.checked);
      updateProgress();
    });
  });
  
  // Initial progress update
  updateProgress();
}

/**
 * Update progress display
 */
function updateProgress() {
  const progressContainer = document.querySelector('.checklist-progress');
  if (!progressContainer) return;
  
  // Get all checkboxes and count completed items
  const checkboxes = document.querySelectorAll('.checklist-item input[type="checkbox"]');
  const totalItems = checkboxes.length;
  let completedItems = 0;
  
  checkboxes.forEach(checkbox => {
    if (checkbox.checked) {
      completedItems++;
    }
  });
  
  // Calculate progress percentage
  const progressPercentage = totalItems > 0 ? Math.round((completedItems / totalItems) * 100) : 0;
  
  // Update progress bar
  const progressBar = progressContainer.querySelector('.progress-bar');
  if (progressBar) {
    progressBar.style.width = `${progressPercentage}%`;
    progressBar.setAttribute('aria-valuenow', progressPercentage);
  }
  
  // Update progress text
  const progressText = progressContainer.querySelector('.progress-text');
  if (progressText) {
    progressText.textContent = `${completedItems} of ${totalItems} tasks completed (${progressPercentage}%)`;
  }
}

/**
 * Set up progress tracking
 */
function setupProgressTracking() {
  // Add reset button functionality if it exists
  const resetButton = document.querySelector('.reset-checklist');
  if (resetButton) {
    resetButton.addEventListener('click', function() {
      if (confirm('Are you sure you want to reset your checklist progress? This cannot be undone.')) {
        resetProgress();
      }
    });
  }
}

/**
 * Reset all progress
 */
function resetProgress() {
  // Get all checkboxes
  const checkboxes = document.querySelectorAll('.checklist-item input[type="checkbox"]');
  
  // Uncheck all checkboxes and remove from localStorage
  checkboxes.forEach(checkbox => {
    checkbox.checked = false;
    localStorage.removeItem(`checklist-${checkbox.id}`);
  });
  
  // Update progress display
  updateProgress();
}

// Initialize when DOM is ready
document.addEventListener('DOMContentLoaded', initPage);

export default {
  initPage
};
